<template>
  <v-card>
    <v-card-title>{{menuTitle}}</v-card-title>
    <v-card-text>
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
        >
        <v-text-field
          v-model="titulo"
          :counter="35"
          :rules="titleRules"
          label="Titulo"
          required
        ></v-text-field>
        <v-textarea
          v-model="descripcion"
          :rules="descripcionRules"
          label="Descripción"
          :counter="250"
          required
          >
        </v-textarea>
        <v-row>
          <v-col>
            <v-text-field
              v-model="fecha_apertura"
              type="date"
              :rules="fechaAperturaRules"
              label="Fecha Apertura"
              required
              >
            </v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              v-model="hora_apertura"
              type="time"
              :rules="horaAperturaRules"
              label="Hora Apertura"
              required
              >
            </v-text-field>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-text-field
              v-model="fecha_cierre"
              type="date"
              :rules="fechaCierreRules"
              label="Fecha Cierre"
              required
              >
            </v-text-field>
          </v-col>
          <v-col>
            <v-text-field
              v-model="hora_cierre"
              type="time"
              :rules="horaCierreRules"
              label="Hora Cierre"
              required
              >
            </v-text-field>
          </v-col>
        </v-row>
        <v-row no-gutters>
          <v-file-input
            prepend-icon
            multiple
            v-model="files"
            label="Adjuntos"
            placeholder="terminos de referencia, descripciones adicionales etc"
            required
          ></v-file-input>
        </v-row>
        <div v-if="!['lista_corta', 'conv_publica'].includes(tipoContratacion)">
          <h2>Datos Personales</h2>
          <v-row>
            <v-col cols="12" md="6">
              <v-text-field
                v-model="dirNombre1"
                :counter="35"
                :rules="dirNombreRules"
                label="Nombre 1"
                required
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6">
              <v-text-field
                v-model="dirNombre2"
                :counter="35"
                :rules="dirNombre2Rules"
                label="Nombre 2"
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6">
              <v-text-field
                v-model="dirApellido1"
                :counter="35"
                :rules="dirApellidoRules"
                label="Apellido 1"
                required
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="6">
              <v-text-field
                v-model="dirApellido2"
                :counter="35"
                :rules="dirApellido2Rules"
                label="Apellido 2"
              ></v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field
                v-model="dirCorreo"
                :counter="35"
                :rules="dirCorreoRules"
                label="Correo"
                required
              ></v-text-field>
            </v-col>
          </v-row>
        </div>
        <div v-if="tipoContratacion === 'lista_corta'">
          <Proponentes
            :loading="proponentesLoading"
            :addProponentes="false"
            :proponentes="proponentes"
            @save="addProponente"
          />  
        </div>
        <v-row justify="end" class='mt-5' no-gutters>
          <v-btn
            color="error"
            class="mr-4"
            @click="reset"
            >Limpiar
          </v-btn>
          <v-btn
            color="success"
            @click="save"
            >{{btnSave}}
          </v-btn>
        </v-row>
      </v-form>
      <v-dialog
        v-model="dialogSaving"
        persistent
        width="300"
        >
        <v-card
          color="primary"
          dark
        >
          <v-card-text>
            Cargando...
            <v-progress-linear
              indeterminate
              color="white"
              class="mb-0"
            ></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-card-text>
  </v-card>
</template>
<script>
import { propConv } from '@/servicios/propuestaConvocatoria'
import { termRef } from '@/servicios/TerminosReferencia.js'
import Proponentes from '@/components/propConvocatoria/Proponentes.vue'
import moment from 'moment';
export default {
  props: ['selPropCont'],
  components: { Proponentes },
  data () {
    return {
      valid: true,
      titulo: '',
      files: [],
      proponentesLoading: false,
      titleRules: [
        v => !!v || 'Titulo es requerido',
        v => (v && v.length <= 35) || 'Titulo debe ser menor de 35 caracteres.',
      ],
      descripcion: '',
      descripcionRules: [
        v => !!v || 'Descripcion es requerido',
        v => (v && v.length <= 250) || 'Descripcion debe ser menor de 250 caracteres.',
      ],
      fecha_cierre: '',
      fechaCierreRules: [
        v => !!v || 'Fecha de cierre es requerido',
        v => (this.difDays(this.fecha_apertura, v)) <= 0 || 'Fecha de cierre debe ser mayor a la fecha de apertura.'
      ],
      hora_cierre: '',
      horaCierreRules: [
        v => !!v || 'Hora de cierre es requerido',
        v => !((this.difDays(this.fecha_apertura, this.fecha_cierre) === 0) && v <= this.hora_apertura) || 'Hora de cierre debe ser mayor a la hora de apertura.'
      ],
      fecha_apertura: '',
      fechaAperturaRules: [
        v => !!v || 'Fecha de apertura es requerido',
        v => (this.difDays(moment(new Date()), v)) <= 0 || 'Fecha de apertura debe ser mayor a la fecha actual.'
      ],
      hora_apertura: '',
      horaAperturaRules: [
        v => !!v || 'Hora de apertura es requerido'
      ],
      dirUsuario: '',
      dirUsuarioRules: [
        v => !!v || 'Usuario es requerido',
        v => (v && v.length <= 35) || 'Usuario debe ser mayor de 35 caracteres.',
      ],
      dirNombre1: '',
      dirNombre2: '',
      dirNombreRules: [
        v => !!v || 'Nombre es requerido',
        v => (v && v.length <= 35) || 'Nombre debe ser mayor de 35 caracteres.',
      ],
      dirNombre2Rules: [
        v => (v.length <= 35) || 'Nombre debe ser mayor de 35 caracteres.',
      ],
      dirApellido1: '',
      dirApellido2: '',
      dirApellidoRules: [
        v => !!v || 'Apellido es requerido',
        v => (v && v.length <= 35) || 'Apellido debe ser mayor de 35 caracteres.',
      ],
      dirApellido2Rules: [
        v => (v.length <= 35) || 'Apellido debe ser mayor de 35 caracteres.',
      ],
      dirCorreo: '',
      dirCorreoRules: [
        v => !!v || 'Correo es requerido',
        v => /.+@.+\..+/.test(v) || 'Correo incorrecto',
      ],
      proponentes: [],
      dialogSaving: false
    }
  },
  async mounted() {
    if (this.selPropCont.referencia) {
      this.titulo = this.selPropCont.titulo
      this.descripcion = this.selPropCont.descripcion
      this.fecha_cierre = this.selPropCont.fecha_cierre.slice(0, 10)
      this.hora_cierre = this.selPropCont.fecha_cierre.slice(11, 16)
      this.fecha_apertura = this.selPropCont.fecha_apertura.slice(0, 10)
      this.hora_apertura = this.selPropCont.fecha_apertura.slice(11, 16)
      // setting el form para el proponente deacuerdo al tipo de contratacion
    }
    await this.getTermRefsDetails(this.selPropCont.id_termref)
  },
  methods: {
    async save () {
      if (this.$refs.form.validate()) {
        // setting clone with new changes
        const clone = { ...this.selPropCont }
        clone.titulo = this.titulo
        clone.descripcion = this.descripcion
        clone.fecha_cierre = this.fecha_cierre + ' ' + this.hora_cierre
        clone.fecha_apertura = this.fecha_apertura + ' ' + this.hora_apertura
        if (!['lista_corta', 'conv_publica'].includes(this.tipoContratacion)) {
          clone.nombre1 = this.dirNombre1
          clone.nombre2 = this.dirNombre2
          clone.apellido1 = this.dirApellido1
          clone.apellido2 = this.dirApellido2
          clone.correo = this.dirCorreo
        } else {
          clone.proponentes = this.proponentes
        }
        // set the form data
        let formData = new FormData()
        for (let i = 0; i < this.files.length; i++) {
          formData.append('files[i]', this.files[i])
        }
        formData.append('titulo', this.titulo)
        formData.append('descripcion', this.descripcion)
        formData.append('fecha_cierre', this.fecha_cierre + ' ' + this.hora_cierre)
        formData.append('fecha_apertura', this.fecha_apertura + ' ' + this.hora_apertura)
        formData.append('estado', clone.estado)
        formData.append('id', clone.id)
        formData.append('tipo_contratacion', clone.tipo_contratacion)
        if (!['lista_corta', 'conv_publica'].includes(this.tipoContratacion)) {
          formData.append('dirNombre1', this.dirNombre1)
          formData.append('dirNombre2', this.dirNombre2)
          formData.append('dirApellido1', this.dirApellido1)
          formData.append('dirApellido2', this.dirApellido2)
          formData.append('dirCorreo', this.dirCorreo)
        } else {
          formData.append('proponentes', JSON.stringify(this.proponentes))
        }
        // send the data
        this.dialogSaving = true
        try {
          if (clone.referencia) { // es edicion
            const { data } = await propConv.editPropuestaConvocatoria(clone.id, formData)
            console.log(data)
            this.$toasted.success(`${clone.referencia} Editada!`, { icon: 'done', duration: 3000 })
            this.$emit('saved', clone)
          } else { // es guardar
            formData.append('estado', 'Abierto')
            clone.estado = 'Abierto'
            const { data } = await propConv.savePropuestaConvocatoria(formData)
            console.log(data)
            if (data.referencia) { // llego el numero de referencia
              const referencia = data.referencia
              clone.referencia = referencia
              this.$toasted.success(`Datos guardados. Con ${referencia}!`, { icon: 'done', duration: 3000 })
              this.$emit('saved', clone)
            } else {
              this.$toasted.info('Error al crear convocatoria.', { icon: 'info', duration: 3000 })
            }
          }
        } catch (error) {
          console.log(error)
          this.$toasted.error(error, { icon: 'error', duration: 3000 })
        }
        this.dialogSaving = false
      }
    },
    reset () {
      this.$refs.form.reset()
    },
    difDays (fechaIni, fechaFin) {
      const now = moment(fechaIni)
      const end = moment(fechaFin)
      const duration = moment.duration(now.diff(end))
      return parseInt(duration.asDays())
    },
    async getTermRefsDetails (id) {
      this.proponentesLoading = true
      try {
        const { data } = await termRef.getTermRefDetail(id)
        this.setPropfields(data)
      } catch (error) {
        console.log(error)
      }
      this.proponentesLoading = false
    },
    setPropfields (data) {
      if  (['lista_corta', 'conv_publica'].includes(this.tipoContratacion)) {
        this.proponentes = data.proponentes.map(item => {
          item.nombre = item.nombre.toUpperCase();
          item.direccion = item.direccion.toUpperCase();
          return item
        })
      }
      else {
        this.dirNombre = data.nombre
        this.dirCorreo = data.correo
      }
    },
    addProponente (prop) {
      if (prop.index || prop.index === 0) this.proponentes.splice(prop.index, 1, prop)
      else this.proponentes.unshift(prop)
    },
  },
  computed: {
    menuTitle () {
      return this.selPropCont.referencia? `Editar ${this.selPropCont.referencia}` : 'Generar Propuesta'
    },
    btnSave () {
      return this.selPropCont.referencia ? 'Actualizar' : 'Guardar'
    },
    tipoContratacion () {
      return this.selPropCont.tipo_contratacion
    }
  }
}
</script>
