<template>
  <div>
    <div class="pa-5">
      <v-row justify="space-between" aling="center">
        <h2>Proponentes</h2>
        <v-btn v-if="addProponentes" small fab @click="visibleForm=!visibleForm" color="primary">
          <v-icon dark v-if="!visibleForm">add</v-icon>
          <v-icon dark v-if="visibleForm">close</v-icon>
        </v-btn>
      </v-row>
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
        v-if="visibleForm"
        >
        <v-row>
          <v-col cols="12" md="6">
            <v-text-field
              v-model="propSelected.nombre"
              :counter="35"
              :rules="dirNombreRules"
              label="Nombre"
              required
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field
              v-model="propSelected.direccion"
              :counter="35"
              :rules="direccionRules"
              label="Dirección"
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field
              v-model="propSelected.telFijo"
              :counter="35"
              :rules="telFijoRules"
              label="Telefono Fijo"
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field
              v-model="propSelected.telCelular"
              :counter="35"
              :rules="telCelularRules"
              label="Telefono Movil"
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <v-text-field
              v-model="propSelected.correo"
              :counter="35"
              :rules="dirCorreoRules"
              label="Correo"
              required
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row justify="end" no-gutters>
          <v-btn
            v-if="!!propSelected.id"
            color="error"
            @click="cancel"
            >volver
          </v-btn>
          <v-btn
            v-if="!propSelected.id"
            color="error"
            class="mr-4"
            @click="reset"
            >Limpiar
          </v-btn>
          <v-btn
            v-if="!propSelected.id"
            color="accent"
            @click="save"
            >{{btnSave}}
          </v-btn>
        </v-row>
      </v-form>
    </div>
    <v-data-table
      v-if="!visibleForm"
      :headers="headers"
      :items="proponentes"
      :items-per-page="5"
      class="elevation-1"
      :loading="loading"
      >
      <template v-slot:item.action="{ item }">
        <v-btn icon @click="editProponente(item)" color="primary">
          <v-icon dark>edit
          </v-icon>
        </v-btn>
        <v-btn icon @click="eliminar(item)" color="error">
          <v-icon dark>delete</v-icon>
        </v-btn>
      </template>
    </v-data-table>
  </div>  
</template>
<script>
export default {
  props: {
    proponentes: { type: Array, default: [] },
    addProponentes: { type: Boolean, default: true },
    loading: { type: Boolean, default: false }
  },
  data () {
    return {
      headers: [
        { text: 'Nombre', value: 'nombre', align: 'center' },
        { text: 'Dirección', value: 'direccion', align: 'center' },
        { text: 'Telefono Fijo', value: 'telFijo', align: 'center' },
        { text: 'Telefono Movil', value: 'telCelular', align: 'center' },
        { text: 'Email', value: 'correo', align: 'center' },
        { text: 'Opciones', value: 'action', align: 'center' }
      ],
      valid: true,
      propSelected: {
        id: '',
        nombre: '',
        direccion: '',
        telFijo: '',
        telCelular: '',
        correo: ''
      },
      dirNombreRules: [
        v => !!v || 'Nombre es requerido',
        v => (v && v.length <= 35) || 'Nombre debe ser menor de 35 caracteres.',
      ],
      direccionRules: [
        v => !!v || 'Dirección es requerido',
        v => (v && v.length >= 10) || 'Dirección debe ser mayor a 10 caracteres.',
      ],
      telFijoRules: [
        v => (v && v.length >= 7)  || 'Debe ser de 7 o mas caracteres.',
        v => /^\d+$(\.\d+)?/.test(v) || 'Debe ser solo numerico.',
      ],
      telCelularRules: [
        v => (v && /3([0-9]{9})/.test(v)) || 'Celular no cumple el formato.',
      ],
      dirCorreoRules: [
        v => !!v || 'Correo es requerido',
        v => /.+@.+\..+/.test(v) || 'Correo incorrecto',
      ],
      visibleForm: false
    }
  },
  methods: {
    async reset () {
      this.$refs.form.reset()
    },
    cancel () {
      this.visibleForm = false
    },
    async save () {
      if (this.$refs.form.validate()) {
        const prop = this.propSelected
        if (!prop.id) this.proponentes.unshift(prop)
        this.visibleForm = false
      }
    },
    editProponente (item) {
      this.propSelected = item
      this.visibleForm = true
    },
    async eliminar (item) {
      const index = this.proponentes.indexOf(item)
      this.proponentes.splice(index, 1)
      this.visibleForm = false
    },
  },
  computed: {
    btnSave () {
      return (this.propSelected.id) ? 'Actualizar' : 'Agregar'
    }
  }
}
</script>

